
//IMAGES
import spanishImage from "../assets/img/spanish.png";
import englishImage from "../assets/img/english.png"
import portugueseImage from "../assets/img/portuguese.png"
import dropdown from "../utils/dropdowns";


function LanguagePill(){
    const languageDropdown = dropdown("dropdown");

    return(
        <div className={`escort_dropdown justify_content_xsm_right`} onClick={()=>languageDropdown.setDropdownState(!languageDropdown.dropdownState)}>
            <div className="language_pill">
              <div className="language_info">
                <img src={spanishImage} alt="" />
                <p>Español</p>
              </div>
              <div className="pill_chevron">
                <span className="material-symbols-outlined">expand_more</span>
              </div>
            </div>
            {/* DropdownMenu */}
            <div className={`dropdown_menu ${languageDropdown.dropdownState ? "":"hidden"}`} id="dropdown">
              <ul>
                <li>
                  <a href="" className="dropdown_option border_bottom">
                    <div className="language_flag">
                    <img src={englishImage} alt="" />
                    </div>
                    <div className="language_name">
                      <p>Inglés</p>
                    </div>
                  </a>
                </li>
                <li>
                  <a href="" className="dropdown_option ">
                    <div className="language_flag">
                    <img src={portugueseImage} alt="" />
                    </div>
                    <div className="language_name">
                      <p>Portugués</p>
                    </div>
                  </a>
                </li>
              </ul>
            </div>
          </div>
    )
}

export default LanguagePill;