import { useState } from "react";
import { deleteData, postDataWithoutPayload } from "../api/fetchData";

import useCommentsToModerate from "../customHooks/useCommentsToModerate";
import { dataSource } from "../api/datasources";
import AdminLeftNavBar from "../components/adminLeftNavBar";
import AdminNumbers from "../components/adminNumbers";
import AdminComments from "../components/adminComments";
import AdminAnnounces from "../components/adminAnnounces";
import AdminScripts from "../components/adminScripts";

/** 
 * 
 * Active pages
 * 0 - Comments
 * 1 - Numbers
 * 2 - Announces
 * 3 - Scripts 
*/
export default function EscortsAdmin() {
  
  //Pages
  const [activePage, setActivePage] = useState(0)

  //Handle left menu click
  let selectedPage: JSX.Element =<div>Hello world!</div>;
  if(activePage === 1) {
    selectedPage = <AdminNumbers setActivePage={setActivePage}/>
  }
  if(activePage === 0){
    selectedPage = <AdminComments setActivePage={setActivePage}/>
  }
  if(activePage === 2){
    selectedPage = <AdminAnnounces setActivePage={setActivePage}/>
  }
  if(activePage === 3){
    selectedPage = <AdminScripts setActivePage={setActivePage}/>
  }

  return (
    <div className="flex_container">
      <AdminLeftNavBar activePage={activePage} setActivePage={setActivePage}/>
      <div className="admin_content">
        {selectedPage}
      </div>
    </div>
  );
}
