async function fetchData(url: string) {
    const URL = url;
    const HEADERS = new Headers({
      "Content-Type": "application/json",
    });
    const myInit = {
      method: "GET",
      headers: HEADERS,
    };
  
    try {
      const apiAnswer = await fetch(URL, myInit);
      // const apiResponse = await apiAnswer.json()
      return apiAnswer
    } catch (err) {
      throw err
    }
  }

