  async function postDataWithPayLoad(url: string, data:FormData){
    const URL = url;
    const HEADERS = new Headers({
      'Content-Type': "multipart/form-data"
    });
    const myInit = {
      method: "POST",
      body: data
    };
  
    try {
      const apiAnswer = await fetch(URL, myInit);
      return apiAnswer
    } catch (err) {
      throw err
    }
  }

