import React, { useState } from "react";
import { useParams } from "react-router-dom";
import { postDataWithPayLoad } from "../api/fetchData";
import EscortAlert from "../components/alert";
import LanguagePill from "../components/languagePill";
import SearchNumber from "../components/searchNumbers";
import language from "../settings/languages/spanish.json"


const initNewNumber: EscortsNewNumber = {
    number:"",
    prefix:0,
    whatsapp: false,
    originWebsite:"",
    owner:"",
    location:"",
    totalSearch:0,
    spamReports:0,
    lineType:"mobile",
    comments:[],
    middleWhatsapp: "none"
    
}
export default function CreateNewNumber(){
    //url info
    const {number} = useParams();

    //alert banner
    const [alert, setAlert] = useState({
        state: true,
        color: "primary_theme",
        text: "Tus alertas aparecerán en esta sección",
        icon: "notifications",
    });

    //Comment Info
    const [newNumberInfo, setNewNumberInfo] = useState<EscortsNewNumber>(initNewNumber)
    const handleChange = (evt: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>
      ) => {
        let id = evt.target.id
        let value:string|number = evt.target.value
        if(id==="prefix"){
          value = parseInt(evt.target.value)
        }
        console.log(newNumberInfo)
        setNewNumberInfo(prevState=>({...prevState, [id]: value}))
    }

    const handleSubmit = async (evt: React.FormEvent) => {
      evt.preventDefault();
      const newNumberFormData = new FormData();

      if(newNumberInfo.middleWhatsapp === "none"){
        setAlert({
            state: true,
            color: "escort_theme",
            text: `Debes seleccionar si el numero cuenta con WhatsApp`,
            icon: "warning",
        }) 
        return
      }

      if(newNumberInfo.middleWhatsapp === "true"){
        newNumberInfo.whatsapp = true
      }else{
        newNumberInfo.whatsapp = false
      }

      newNumberFormData.append("generalInfo", JSON.stringify(newNumberInfo))

      try{
        const apiResponse = await postDataWithPayLoad("http://localhost:3000/numbersApi/number/create", newNumberFormData)
        
        if(apiResponse.status === 400){
            const responseMessage = await apiResponse.json()
            setAlert({
                state: true,
                color: "escort_theme",
                text: `${responseMessage.response}`,
                icon: "warning",
            }) 
            return
        }

        if(apiResponse.status === 200){
            setAlert({
                state: true,
                color: "primary_theme",
                text: `Numero guardado con exito`,
                icon: "check_circle",
            }) 
        }

        setNewNumberInfo(initNewNumber)
      }catch(err){
        console.log("Error")
        setAlert({
            state: true,
            color: "escort_theme",
            text: "Error guardando número",
            icon: "warning",
        })
      }

    }
    return (
        <main>
            <EscortAlert text={alert.text} color={alert.color} state={alert.state} icon={alert.icon} action={setAlert}/>
          <div className="escort_main_number_background">
            <nav className="escorts_nav">
              <LanguagePill />
            </nav>
            <div className="escorts_number_initial grid">
              <div></div>
              <div className="escorts_number_top">
                <div className="escorts_number_title my_sm_16">
                  <h1 className="escorts_number_title_xsm escorts_number_title escorts_number_title_sm escorts_number_title_md escorts_number_title_lg txt_g_center">
                    <span className="txt_white">ESCORTS</span>
                    <span className="txt_red">FAKE</span>
                  </h1>
                  <p className="txt_subtitle txt_white txt_g_center">
                    {language.subtitle}
                  </p>
                </div>
    
                <SearchNumber />
              </div>
              <div></div>
            </div>
          </div>
    
          <section className="escort_page_spacing">
            <h1 className="txt_g_center txt_white number_title my_sm_16">
              {language.number_title}: {number}
            </h1>
            <div className="info_title">
              <h3
                className="txt_white info_title"
                style={{ margin: "2rem 0", fontWeight: "400" }}
              >
                {language.information_title}
              </h3>
            </div>
            <form onSubmit={handleSubmit} className="escorts_form">
                <div className="input_field">
                  <label htmlFor="prefix">Prefijo</label>
                <input type="number" id="prefix" value={newNumberInfo.prefix} onChange={handleChange}/>
                </div>
                <div className="input_field">
                <label htmlFor="number">Número de telefono</label>
                <input type="number" id="number" value={newNumberInfo.number} onChange={handleChange} placeholder="Numero de telefono"/>
                </div>
                <div className="input_field">
                <label htmlFor="originWebsite">Sitio de origen</label>
                <input type="text" id="originWebsite" value={newNumberInfo.originWebsite} onChange={handleChange} placeholder="Sitio de origen"/>
                </div>
                <div className="input_field">
                <label htmlFor="owner">Propietario</label>
                <input type="text" id="owner" value={newNumberInfo.owner} onChange={handleChange} placeholder="Propietario"/>
                </div>
                <div className="input_field">
                <label htmlFor="location">Ubicación</label>
                <input type="text" id="location" value={newNumberInfo.location} onChange={handleChange} placeholder="Ubicación"/>
                </div>
                <div className="input_field">
                <label htmlFor="middleWhatsapp">WhatsApp</label>
                <select name="middleWhatsapp" id="middleWhatsapp" value={newNumberInfo.middleWhatsapp} onChange={handleChange}>
                    <option value="none">¿Tiene WhatsApp?</option>
                    <option value="true">Si</option>
                    <option value="false">No</option>
                </select>
                </div>
                <div className="input_field">
                <button className="btn btn_medium escort_theme">Enviar</button>
                </div>
            </form>
            </section>
        </main>
      );
}