import { useEffect, useState } from "react";
import { dataSource } from "../api/datasources";
import { fetchData } from "../api/fetchData";
import "../types";

export default function useCommentsToModerate() {
  const [comments, setComments] = useState<CommentsModeration[]>([
    {
      _id: "",
      commentText: "",
      commentUsername: "",
      commentModerate: false,
      parentNumber: "",
    },
  ]);

  const updateComments = async () => {
    try {
      const apiPromise = await fetchData(
        `${dataSource}/comment/getAll`
      );
      const apiResponse = (await apiPromise.json()) as CommentsModeration[];
      setComments(apiResponse);
    } catch (err) {
      console.log(err);
    }
  };

  useEffect(() => {
    updateComments();
  }, []);

  return { comments,updateComments };
}
