import { useEffect, useState } from "react";
import { dataSource } from "../api/datasources";
import { fetchData } from "../api/fetchData";
import "../types";

export default function useAds() {
    
  const [ads, setAds] = useState<EscortAds[]>([{
    _id:"",
    name: "",
    description:"",
    ad:""
  }]);

  const updateAds = async () => {
    try {
      const apiPromise = await fetchData(
        `${dataSource}/ads/getAll`
      );
      const apiResponse = (await apiPromise.json()) as EscortAds[];
      setAds(apiResponse);
    } catch (err) {
      console.log(err);
    }
  };

  useEffect(() => {
    updateAds();
  }, []);

  return { ads,updateAds };
}
