import { useEffect, useState } from "react";
import { dataSource } from "../api/datasources";
import {fetchData} from "../api/fetchData";
import "../types";



const initializeNumber:NumberInfo | null = {
_id: "",
comments: [],
prefix: 0,
location: "",
totalSearch:0,
spamReports:0,
owner: "",
lineType:"",
originWebsite:"",
whatsapp:false,
number: "",
}
export default function useNumber(number:string | undefined) {
    const [numberInfo, setNumberInfo] = useState(initializeNumber)

    const updateNumber = async () => {
        try {
            const apiPromise = await fetchData(`${dataSource}/${number}`);
            const apiResponse = await apiPromise.json() as NumberInfo;
            setNumberInfo(apiResponse)

        } catch (err) {
            console.log(err)
        }
    }

    useEffect(() => {
        updateNumber()
    }, [])

    return { numberInfo, updateNumber }
}