import { useEffect, useState } from "react";
import { dataSource } from "../api/datasources";
import { fetchData } from "../api/fetchData";
import "../types";

export default function useNumbersToModerate() {
  const [numbers, setNumbers] = useState<NumberInfo[]>([{
    _id: "",
  comments: [],
  prefix: 0,
  location: "",
  totalSearch: 0,
  spamReports: 0,
  owner: "",
  lineType: "",
  originWebsite: "",
  whatsapp: false,
  number: ""
  }]);

  const updateNumbers = async () => {
    try {
      const apiPromise = await fetchData(
        `${dataSource}/number/getNumbersToModerate`
      );
      const apiResponse = (await apiPromise.json()) as NumberInfo[];
      setNumbers(apiResponse);
    } catch (err) {
      console.log(err);
    }
  };

  useEffect(() => {
    updateNumbers();
  }, []);

  return { numbers,updateNumbers };
}
