import React, { useState } from "react";
import { postDataWithPayLoad } from "../api/fetchData";
import LanguagePill from "./languagePill";
import SearchNumber from "./searchNumbers";

interface NonNullNumberProps {
    numberInfo: NumberInfo
    language: Language
}

const emptyComment = {
  username: "Miguel",
  comment:""
}

export default function NonNullNumber(props: NonNullNumberProps){
    //Comment Info
    const [commentInfo, setCommentInfo] = useState<EscortsComments>(emptyComment)

    const language = props.language
    const numberInfo = props.numberInfo

    const handleChange = (evt: React.ChangeEvent<HTMLTextAreaElement>
      ) => {
        const name = evt.target.name
        const value = evt.target.value
        setCommentInfo(prevState=>({...prevState, [name]: value}))
    }

    const handleSubmit = async (evt: React.FormEvent) => {
      evt.preventDefault();
      const commentInfoJson = {
        commentText: commentInfo.comment,
        commentUsername: commentInfo.username,
        commentModerate: false,
        parentNumber: numberInfo.number
      }

      const commentFormData = new FormData();
      commentFormData.append("commentInfo", JSON.stringify(commentInfoJson))

      try{
        await postDataWithPayLoad("http://localhost:3000/numbersApi/comment", commentFormData)
        setCommentInfo(emptyComment)
      }catch(err){
        console.log(err)
      }

    }
    return (
        <main>
          <div className="escort_main_number_background">
            <nav className="escorts_nav">
              <LanguagePill />
            </nav>
            <div className="escorts_number_initial grid">
              <div></div>
              <div className="escorts_number_top">
                <div className="escorts_number_title my_sm_16">
                  <h1 className="escorts_number_title_xsm escorts_number_title escorts_number_title_sm escorts_number_title_md escorts_number_title_lg txt_g_center">
                    <span className="txt_white">ESCORTS</span>
                    <span className="txt_red">FAKE</span>
                  </h1>
                  <p className="txt_subtitle txt_white txt_g_center">
                    {language.subtitle}
                  </p>
                </div>
    
                <SearchNumber />
              </div>
              <div></div>
            </div>
          </div>
    
          <section className="escort_page_spacing">
            <h1 className="txt_g_center txt_white number_title my_sm_16">
              {language.number_title}: +{numberInfo.prefix} {numberInfo.number}
            </h1>
            <div className="info_title">
              <h3
                className="txt_white info_title"
                style={{ margin: "2rem 0", fontWeight: "400" }}
              >
                {language.information_title}
              </h3>
            </div>
            <div
              className="info_items txt_white grid col_1 col_xsm_2 col_sm_3"
              style={{ margin: "2rem 0" }}
            >
              <div className="views info_structure">
                <div className="icon">
                  <span className="material-symbols-outlined">visibility</span>
                </div>
                <p>{numberInfo.totalSearch} {language.views_text}</p>
              </div>
              <div className="comments info_structure">
                <div className="icon">
                  <span className="material-symbols-outlined">chat_bubble</span>
                </div>
                <p>{numberInfo.comments.length} {language.comments_text}</p>
              </div>
              <div className="spam_reports info_structure">
                <div className="icon">
                  <span className="material-symbols-outlined">warning</span>
                </div>
                <p>{numberInfo.spamReports} {language.reports_text}</p>
              </div>
            </div>
            <div className="owner_info txt_white">
              <p className="owner_text">{language.owner_text}: {numberInfo.owner}</p>
              <p className="owner_text">{language.location_text}: {numberInfo.location}</p>
              <p className="owner_text">
                {language.origin_text}:{" "}
                <a href={numberInfo.originWebsite} className="origin_link">{numberInfo.originWebsite}</a>
              </p>
              <p className="owner_text">
                {language.whatsapp_text}: {numberInfo.whatsapp ? "Sí" : "No"}
              </p>
            </div>
          </section>
          <section className="charts escort_page_spacing grid col_sm_2">
            <div className="circular_chart">
              <div
                className="pie animate"
              >
                {" "}
                83%
              </div>
            </div>
            <div className="bar_chart">
              <canvas id="barChart"></canvas>
            </div>
          </section>
          <section className="comments_section grid col_1 col_sm_2 escort_page_spacing gap_g_16">
            <form className="comments_form" onSubmit={handleSubmit}>
            <div className="escort_card">
              <div className="escort_card_title">Deja tu comentario</div>
              <div className="escort_card_body">
                <textarea
                  name="comment"
                  id="comment"
                  className="escort_comment_box"
                  placeholder="Añade tu comentario"
                  value={commentInfo.comment}
                  onChange={handleChange}
                ></textarea>
              </div>
            </div>
            <button className="btn escort_theme round_8">Enviar</button>
            </form>
            <div className="escort_card">
              <div className="escort_card_title">{language.classify_text}</div>
              <div className="escort_card_body clasify_number">
                <form action="">
                  <div className="input_container">
                    <label htmlFor="safe" className="input_field">
                      {" "}
                      <input type="radio" /> {language.safe_number}
                    </label>
                  </div>
                  <div className="input_container">
                    <label htmlFor="safe" className="input_field">
                      {" "}
                      <input type="radio" /> {language.neutral_number}
                    </label>
                  </div>
                  <div className="input_container">
                    <label htmlFor="safe" className="input_field">
                      {" "}
                      <input type="radio" /> {language.no_answer_number}
                    </label>
                  </div>
                </form>
              </div>
            </div>
          </section>
          <section className="old_comments grid col_1 escort_page_spacing gap_g_16">
            <h3 className="comment_title">{language.comments_text}</h3>
            {numberInfo.comments.map((comment, index) => {
              return (
                <div className="comment_card" key={index}>
                  <div className="comment_card_title">
                    <p>{comment.username}</p>
                  </div>
                  <div className="comment_card_body">
                    <p>
                      {comment.comment}
                    </p>
                  </div>
                </div>
              );
            })}
          </section>
        </main>
      );
}