import { dataSource } from "../api/datasources";
import { deleteData, postDataWithoutPayload, postDataWithPayLoad, putData } from "../api/fetchData";
import userImage from "../assets/img/user_image.png";
import { useEffect, useState } from "react";
import AdminSearch from "./adminSearch";
import useScripts from "../customHooks/useScripts";
import TopNav from "./topNav";

export default function AdminScripts({setActivePage}:GeneralAdmin) {
  const { scripts, updateScripts } = useScripts();

  //Scripts to be filtered and show as results of search
  const [filteredScripts, setFilteredScripts] = useState(scripts);

  //Create scripts
  const [newScriptInfo, setNewScriptInfo] = useState({
    name:"",
    description: "",
    script:""
  })

  //Info of the comment selected by the user in the table of comments
  const [selectedScript, setSelectedScript] = useState(scripts[0]);

  //Admin is editing
  const [adminEditing, setAdminEditing] = useState(false);

  //Admin is creating
  const [adminCreating, setAdminCreating] = useState(false);

  const handleScriptDeletion = async (scriptId: string) => {
    try {
      await deleteData(`${dataSource}/scripts/${scriptId}/delete`);
      updateScripts();
    } catch (err) {
      console.log(err);
    }
  };

  const showScript = (scriptId: string) => {
    //Set the visibility of editor
    setAdminEditing(true);
    const userSelectedScript = scripts.find(
      (script) => script._id === scriptId
    );
    if (userSelectedScript) {
      setSelectedScript(userSelectedScript);
    }
  };

  const handleChange = (evt: React.ChangeEvent<HTMLInputElement>) => {
    const value = evt.target.value;
    const id = evt.target.id;
    setSelectedScript((prevState) => ({ ...prevState, [id]: value }));
  };

  const handleNewChange = (evt: React.ChangeEvent<HTMLInputElement>) => {
    const value = evt.target.value;
    const id = evt.target.id;
    setNewScriptInfo((prevState) => ({ ...prevState, [id]: value }));
  };

  const handleSubmit = async (evt: React.FormEvent<HTMLFormElement>) => {
    evt.preventDefault();
    const selectedCommentInfo = new FormData();
    selectedCommentInfo.append("scriptInfo", JSON.stringify(selectedScript));
    const apiResponse = await putData(
      `${dataSource}/scripts/${selectedScript._id}/update`,
      selectedCommentInfo
    );
    updateScripts();
  };

  const handleCreation = async (evt: React.FormEvent<HTMLFormElement>) => {
    evt.preventDefault();
    const newScript = new FormData();
    newScript.append("scriptInfo", JSON.stringify(newScriptInfo));
    const apiResponse = await postDataWithPayLoad(
      `${dataSource}/scripts/create`,
      newScript
    );
    updateScripts();
  };

  //update filtered Elements
  useEffect(() => {
    setFilteredScripts(scripts);
  }, [scripts]);

  let tableContent: JSX.Element;

  if (filteredScripts.length === 0) {
    tableContent = (
      <tbody>
        <tr>
          <td colSpan={3}>No hay scripts para moderar...</td>
        </tr>
      </tbody>
    );
  } else {
    tableContent = (
      <tbody>
        {filteredScripts.map((script) => {
          return (
            <tr key={script._id} onClick={() => showScript(script._id)}>
              <td>{script.name}</td>
              <td>{script.description}</td>
              <td>
                <div
                  className="btn_small escort_theme"
                  onClick={() => handleScriptDeletion(script._id)}
                >
                  Eliminar
                </div>
              </td>
            </tr>
          );
        })}
      </tbody>
    );
  }

  return (
    <section className="comments_moderation">
      <TopNav title="SCRIPTS" setActivePage={setActivePage}/>
      <main className="escort_admin_page">
      <div className="add_new_container my_sm_16">
          <button className="btn success_theme" onClick={()=>setAdminCreating(true)}>Añadir</button>
        </div>
        <AdminSearch
          propToSearch="description"
          originalArray={scripts}
          setFiltered={setFilteredScripts}
        />
        <div className="table_container">
          <table className="escorts_table my_sm_16">
            <thead>
              <tr>
                <td>Nombre</td>
                <td>Descripción</td>
                <td></td>
              </tr>
            </thead>
            {tableContent}
          </table>
        </div>
        <div className={`admin_editor ${adminEditing ? "show_container" : "hide"}`}>
          <div className="editor_header">
            <h2 className="txt_white">Editar Script</h2>
            <div
              className="close_editor"
              onClick={() => setAdminEditing(false)}
            >
              <span className="material-symbols-outlined">cancel</span>
            </div>
          </div>
          <form className="escorts_form" onSubmit={handleSubmit}>
            <div className=" input_field_sm">
              <label htmlFor="name">Nombre del script</label>
              <input
                type="text"
                id="name"
                value={selectedScript.name}
                onChange={handleChange}
              />
            </div>
            <div className=" input_field_sm">
              <label htmlFor="description">Descripción</label>
              <input
                type="text"
                id="description"
                value={selectedScript.description}
                onChange={handleChange}
              />
            </div>
            <div className=" input_field_sm">
              <label htmlFor="script">Script</label>
              <input
                type="text"
                id="script"
                value={selectedScript.script}
                onChange={handleChange}
              />
            </div>
            <button className="btn escort_theme">Modificar</button>
          </form>
        </div>
        {/* Create new script */}
        <div className={`admin_editor ${adminCreating ? "show_container" : "hide"}`}>
          <div className="editor_header">
            <h2 className="txt_white">Añadir nuevo Script</h2>
            <p>Los scripts que cargues serán cargados en todas las páginas</p>
            <div
              className="close_editor"
              onClick={() => setAdminCreating(false)}
            >
              <span className="material-symbols-outlined">cancel</span>
            </div>
          </div>
          <form className="escorts_form" onSubmit={handleCreation}>
            <div className=" input_field_sm">
              <label htmlFor="name">Nombre del script</label>
              <input
                type="text"
                id="name"
                value={newScriptInfo.name}
                onChange={handleNewChange}
              />
            </div>
            <div className=" input_field_sm">
              <label htmlFor="description">Descripción</label>
              <input
                type="text"
                id="description"
                value={newScriptInfo.description}
                onChange={handleNewChange}
              />
            </div>
            <div className=" input_field_sm">
              <label htmlFor="script">Script</label>
              <input
                type="text"
                id="script"
                value={newScriptInfo.script}
                onChange={handleNewChange}
              />
            </div>
            <button className="btn escort_theme">Crear script</button>
          </form>
        </div>
      </main>
    </section>
  );
}
