import { useEffect, useState } from "react";

function dropdown(dropdownId: string){
    const [dropdownState, setDropdownState] = useState(false)

      
  const handleDropdown = (e:any) => {
    const x = e.clientX
    const y = e.clientY

    const dropdown = document.getElementById(dropdownId) as HTMLDivElement;
    const dropdownOffsets = dropdown.getBoundingClientRect();

    if(dropdownState && ((x<dropdownOffsets.x || x>(dropdownOffsets.x + dropdownOffsets.width)) || (y>dropdownOffsets.y || y<(dropdownOffsets.height))) ){
      setDropdownState(!dropdownState)
    }
  }

   useEffect(()=>{
    window.onclick = handleDropdown
   },[dropdownState])

   return {dropdownState, setDropdownState}
}

export default dropdown;