hite;\n    margin-bottom: 1rem;\n}\n\n.body_big{\n    font-size: 2rem;\n}\n\n//this is xsm - extra small\n@media (min-width: 599px) {\n    .txt_xsm_center{\n        text-align: center;\n    }\n\n    .escorts_title_xsm{\n        font-size: 4rem;\n    }\n\n}\n\n//this is sm - small\n@media (min-width: 904px) {\n    .txt_sm_center{\n        text-align: center;\n    }\n\n    .escorts_title_sm{\n        font-size: 4.5rem;\n    }\n}\n\n//this is md - medium\n@media (min-width: 1239px) {\n    .txt_md_center{\n        text-align: center;\n    }\n\n    .escorts_title_md{\n        font-size: 6rem;\n    }\n}\n\n//this is large\n@media (min-width: 1440px) {\n    .txt_lg_center{\n        text-align: center;\n    }\n\n    .escorts_title_lg{\n        font-size: 8rem;\n    }\n}\n\n",".flex{\n    display: flex;\n}\n\n.flex_sm_colum{\n    flex-direction: column;\n}",".btn{\n    border: none;\n    padding: 0.75rem 1rem;\n    cursor: pointer;\n    width: fit-content;\n    text-decoration: none;\n}\n\n.btn_small{\n    border: none;\n    padding: 0.5rem 0.75rem;\n    width: fit-content;\n    cursor: pointer;\n}\n\n.btn_medium{\n    border: none;\n    padding: 1rem 1.25rem;\n    width: fit-content;\n    cursor: pointer;\n    //Font\n    font-size: 1rem;\n}\n\n.round_8{\n    border-radius: 8px;\n}","\n\n.grid{\n    display: grid;\n}\n\n.col_1 {\n    grid-template-columns: 1fr;\n}\n\n.col_3 {\n    grid-template-columns: repeat(3, 1fr);\n}\n\n.col_6{\n    grid-template-columns: repeat(6, 1fr);\n}\n\n.col_2{\n    grid-template-columns: repeat(1, 1fr);\n}\n\n.span_sm_2{\n    grid-column: span 2;\n}\n\n.inline_grid{\n    display: inline-grid;\n}\n\n.flex_container{\n    display: flex;\n}\n\n.flex_item {\n    /* flex: 1 auto; */\n    flex: 1 auto;\n    overflow-x: scroll;\n    white-space: nowrap;\n    background: red;\n\n}\n\n\n///Create columns for the grid\n@mixin grid_mixin($count, $screenSize) {\n    @for $i from 1 through $count{\n        .col_#{$screenSize}_#{$i} { \n            grid-template-columns: repeat(#{$i}, auto);\n         }\n    }\n}\n\n///Create spans for the grid\n@mixin grid_span_mixin($count, $screenSize) {\n    @for $i from 1 through $count{\n        .span_#{$screenSize}_#{$i} { \n            grid-column: span #{$i};\n         }\n    }\n}\n\n///Create gaps for the grid\n@mixin grid_gap_mixin($count, $screenSize, ) {\n    @for $i from 1 through $count{\n        .gap_#{$screenSize}_#{$i*4} { \n            gap: #{$i * 0.25rem};\n         }\n    }\n}\n\n@include grid_gap_mixin(12, g);\n\n//this is xsm - extra small\n@media (min-width: 599px) {\n    @include grid_mixin(12, xsm);\n    @include grid_span_mixin(12, xsm);\n    @include grid_gap_mixin(12, xsm);\n\n    .justify_content_xsm_center{\n        justify-content: center;\n    }\n\n    .justify_content_xsm_right{\n        justify-content: right;\n    }\n\n    .alig_items_xsm_center{\n        align-items: center;\n    }\n}\n\n//this is sm - small\n@media (min-width: 904px) {\n    @include grid_mixin(12, sm);\n    @include grid_span_mixin(12, sm);\n    @include grid_gap_mixin(12, sm);\n\n    .justify_content_sm_center{\n        justify-content: center;\n    }\n\n    .alig_items_sm_center{\n        align-items: center;\n    }\n}\n\n//this is md - medium\n@media (min-width: 1239px) {\n    @include grid_mixin(12, md);\n    @include grid_span_mixin(12, md);\n    @include grid_gap_mixin(12, md);\n\n    .justify_content_md_center{\n        justify-content: center;\n    }\n\n    .alig_items_md_center{\n        align-items: center;\n    }\n}\n\n//this is large\n@media (min-width: 1440px) {\n    @include grid_mixin(12, lg);\n    @include grid_span_mixin(12, lg);\n    @include grid_gap_mixin(12, lg);\n\n    .justify_content_lg_center{\n        justify-content: center;\n    }\n\n    .alig_items_lg_center{\n        align-items: center;\n    }\n}","$primary: #FDBE33;\n$escort_red: #8C0000;\n$secondary: #030F27;\n$tertiary: #EFE1C8;\n$success: rgb(32, 197, 32);\n\n//TEXT COLORS\n.txt_white{\n    color: white;\n}\n\n//THEMES\n.primary_theme{\n    background-color: $primary;\n    color: $secondary;\n}\n\n.secondary_theme{\n    background-color: $secondary;\n    color: $primary;\n}\n\n.escort_theme{\n    background-color: $escort_red;\n    color: white;\n}\n\n.error_theme{\n    background-color: rgb(255, 49, 49);\n    color: white\n}\n\n.success_theme{\n    background-color: $success;\n    color: white;\n}\n\n.txt_primary{\n    color: $primary;\n}\n\n.txt_secondary{\n    color: $secondary;\n}\n\n//background\n.bg_primary{\n    background-color: $primary;\n}\n\n.bg_secondary{\n    background-color: $secondary;\n}\n\n.txt_red{\n    color: #8C0000;\n}\n","@use \"colors\";\n\n.number_card{\n    background-color: white;\n    //Spacing and paddings\n    padding: 1rem 2rem;\n    //Borders\n    border-radius: 0.5rem;\n\n    .card_title{\n        display: flex;\n        gap: 0.75rem;\n\n        .country_name{\n            p{\n                font-family: 'Ovo', serif;\n            }\n        }\n    }\n\n    .number_info{\n        p{\n            font-family: 'Ovo', serif;\n            font-size: 2rem;\n        }\n    }\n\n    .number_footer{\n        display: flex;\n        gap: 12px;\n        .spam_reports{\n            display: flex;\n            align-items: center;\n            gap: 0.5rem;\n        }\n\n        .views_reports{\n            display: flex;\n            align-items: center;\n            gap: 0.5rem;  \n        }\n    }\n}\n\n.escort_card{\n    height: 100%;\n    display: flex;\n    flex-direction: column;\n    .escort_card_title{\n        background-color: colors.$escort_red;\n        color: white;\n        font-size: 2.5rem;\n        //border\n        border-top-right-radius: 1rem;\n        border-top-left-radius: 1rem;\n        //spacing\n        padding: 0.25rem 1rem;\n    }\n    .escort_card_body{\n        background-color: white;\n        border-bottom-right-radius: 1rem;\n        border-bottom-left-radius: 1rem;\n        height: 100%;\n    }\n}\n\n.escort_comment_box{\n            resize: none;\n            border-bottom-right-radius: 1rem;\n            border-bottom-left-radius: 1rem;\n            border: none;\n            //font\n            font-size: 2.5rem;\n            //spacing\n            padding: 0.25rem 1rem;\n            //width\n            width: 100%;\n            height: 100%;\n}\n\n.comment_card{\n    background-color: white;\n    padding: 2rem 1rem;\n    border-radius: 0.75rem;\n    .comment_card_title{\n        p{\n            font-size: 3rem;\n        }\n    }\n\n    .comment_card_body{\n        p{\n            font-size: 2rem;\n        }\n    }\n}",".my_sm_16{\n    margin-top: 16px;\n    margin-bottom: 16px;\n}",".escorts_main_right {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n\n    img {\n        width: 70%;\n    }\n}\n\n.escorts_main_left {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n}\n\n.escort_main_background {\n    padding: 6rem 2.5rem;\n    background-image: linear-gradient(to bottom, rgba(75, 75, 75, 0.52), rgba(0, 0, 0, 0.73)), url(\"../background.png\");\n    background-size: cover;\n    background-color: black;\n}\n\n.escort_main_number_background {\n    padding: 4rem 2.5rem;\n    background-image: linear-gradient(to bottom, rgba(75, 75, 75, 0.52), rgb(0, 0, 0)), url(\"../background.png\");\n    background-size: cover;\n    background-color: black;\n}\n\n.number_collection {\n    margin: 0 2.5rem;\n}\n\n.escorts_number_initial {\n    grid-template-columns: 1fr 4fr 1fr;\n}\n\n.info_structure {\n    display: flex;\n    gap: 0.5rem;\n    // justify-content: center;\n    align-items: center;\n\n    font-size: 40px;\n\n    .icon {\n        span {\n            display: flex;\n            align-items: center;\n            font-size: 40px;\n        }\n    }\n}\n\n.escort_page_spacing {\n    padding: 3rem 6rem;\n}\n\n.escort_admin_page {\n    padding: 2rem 1rem;\n}\n\n.clasify_number {\n    display: flex;\n    flex-direction: column;\n    justify-content: space-around;\n}\n\n.circular_chart {\n    margin: auto auto;\n}\n\n.fullscreen_landing {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    height: 100vh;\n\n    background-image: linear-gradient(to bottom, rgba(75, 75, 75, 0.575), rgba(0, 0, 0, 0.753)), url(\"../background.png\");\n    background-size: cover;\n    background-color: black;\n}\n\n.full_search {\n    width: 70vw;\n}\n\n.escorts_left_panel_container {\n    flex-basis: 300px;\n    flex-shrink: 0;\n}\n\n.admin_content {\n    width: 100%;\n    overflow: auto;\n    .top_nav {\n        display: flex;\n        color: white;\n        justify-content: space-between;\n        align-items: center;\n\n        //margin\n        padding: 3rem 1rem;\n\n        .user_section {\n            display: flex;\n            gap: 12px;\n\n            .user_image {\n                img {\n                    width: 50px;\n                }\n            }\n\n            .username {\n                display: flex;\n                align-items: center;\n            }\n        }\n    }\n}\n\n.escorts_left_panel {\n    position: fixed;\n    min-height: 100vh;\n    width: 300px;\n}\n\n.left_panel_title {\n    padding: 3rem 1rem;\n}\n\n.left_panel_menu ul {\n    padding-left: 0;\n    li{\n        cursor: pointer;\n        border-radius: 0 100vw 100vw 0;\n        &:hover{\n            background-color: #610000;\n        }\n        &.active{\n            background-color: #8C0000;\n        }\n    }\n}\n\n.menu_item {\n    display: flex;\n    padding: 1rem 1.5rem;\n    gap: 12px;\n\n    .menu_icon {\n        color: white;\n    }\n\n    .menu_text {\n        color: white;\n    }\n}\n\n\n\n.layoutGrid {\n    display: grid;\n    grid-template-columns: auto 1fr;\n}\n\n// ALERTS //\n.escort_alert_container {\n    display: flex;\n    padding: 0.75rem 0.75rem;\n    justify-content: space-between;\n    position: fixed;\n    width: 100%;\n    &.hidden{\n        display: none;\n    }\n    .alert_info {\n        display: flex;\n        align-items: center;\n    }\n}\n\n.table_container{\n    width: 100%;\n    overflow-x: auto;\n}\n\n//Admin editor\n.admin_editor{\n    .editor_header{\n        display: flex;\n    justify-content: space-between;\n\n    span{\n        color: white;\n        cursor: pointer;\n        user-select: none;\n    }\n    }\n}\n\n.add_number_container{\n    display: flex;\n    flex-direction: column;\n}\n\n\n//mobile menu\n.mobile_menu{\n    position: fixed;\n    z-index: 10;\n    background-color: black;\n    top: 0;\n    left: 0;\n    bottom: 0;\n    width: 100%;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    ul{\n        padding: 0;\n        li{\n            list-style: none;\n        }\n    }\n}\n\n.close_mobile_menu{\n    position: absolute;\n    top: 15px;\n    right: 15px;\n}",".escorts_table{\n    border-collapse: collapse;\n    width: 100%;\n    thead{\n        td:first-child{\n            border-radius: 8px 0px 0 0;\n        }\n        td:last-child{\n            border-radius: 0px 8px 0 0;\n        }\n        td{\n            //padding\n            padding: 0.25rem 1rem;\n            background-color: #8C0000;\n            color: white;\n        }\n    }\n    tbody{\n        tr{\n            &:hover{\n                td{\n                    background-color: rgb(218, 218, 218);\n                cursor: pointer;\n                }\n            }\n        }\n        td{\n            padding: 0.25rem 1rem;\n            background-color: white;\n        }\n        tr:last-child{\n            td:first-child{\n                border-radius: 0px 0px 0px 8px;\n            }\n            td:last-child{\n                border-radius: 0px 0px 8px 0px;\n            }\n        }\n    }\n}","@use \"colors\";\n\n.escorts_search_container{\n    background-color: white;\n    display: flex;\n    //border\n    border-radius: 100vw;\n\n    .country_selector{\n        display: flex;\n        align-items: center;\n        gap: 12px;\n        justify-content: space-between;\n\n        //Border\n        border-right: solid rgb(131, 131, 131) 1px;\n\n        span{\n            display: flex;\n            align-items: center;\n        }\n        img{\n            width: 24px;\n            aspect-ratio: 1/1;\n            border-radius: 50%;\n        }\n        .language_info{\n            display: flex;\n            gap: 12px;\n            align-items: center;\n        }\n    }\n\n    .search_container{\n        width: 100%;\n        display: flex;\n        align-items: center;\n        input{\n            padding-left: 1rem;\n            display: block;\n            height: 100%;\n            border: none;\n            width: 100%;\n            background-color: transparent;\n            font-size: 20px;\n            font-family: 'Ovo', serif;\n        }\n        .search_button{\n            position: relative;\n            padding: 0.75rem;\n            background-color: transparent;\n            border: none;\n            cursor: pointer;\n            .search_container{\n                background-color: colors.$escort_red;\n                border-radius: 50%;\n                padding: 0.25rem;\n\n                span{\n                    font-size: 18px;\n                    color: white;\n                }\n            }\n        }\n    }\n}\n\n.input_field{\n    display: flex;\n    padding:1rem 1rem ;\n    font-size: 2.5rem;\n\n    input{\n        margin-right: 1rem;\n    }\n}\n\ninput[type=\"radio\"] {\n    -webkit-appearance: none;\n    appearance: none;\n    background-color: #fff;\n    margin: 0;\n    font: inherit;\n    color: currentColor;\n    width: 1.15em;\n    height: 1.15em;\n    border: 0.15em solid currentColor;\n    border-radius: 50%;\n\n    transform: translateY(-0.075em);\n\n    display: grid;\n    place-content: center;\n  }\n\n  input[type=\"radio\"]::before {\n    content: \"\";\n    width: 0.65em;\n    height: 0.65em;\n    border-r