//IMAGES
import spanishImage from "../assets/img/spanish.png";
import englishImage from "../assets/img/english.png"
import portugueseImage from "../assets/img/portuguese.png"
import React, { useEffect, useState } from "react";
import dropdown from "../utils/dropdowns";
import { useNavigate } from "react-router-dom";


function SearchNumber(){
    //Search
    const [search, setSearch] = useState("");
    const numberDropdown = dropdown("numberDropdown")
    const navigate = useNavigate()

    const handleSubmit = (evt: React.FormEvent) => {
      evt.preventDefault()
      navigate(`/number/${search}`)
    }
    return(
        <div className="escorts_search_container">
              <div className="country_selector">
              <div className={`escort_dropdown justify_content_xsm_right`} onClick={()=>numberDropdown.setDropdownState(!numberDropdown.dropdownState)}>
            <div className="language_pill">
              <div className="language_info">
                <img src={spanishImage} alt="" />
                <p>España</p>
              </div>
              <div className="pill_chevron">
                <span className="material-symbols-outlined">expand_more</span>
              </div>
            </div>
            {/* DropdownMenu */}
            <div className={`dropdown_menu ${numberDropdown.dropdownState ? "":"hidden"}`} id="numberDropdown">
              <ul>
                <li>
                  <a href="" className="dropdown_option border_bottom">
                    <div className="language_flag">
                    <img src={englishImage} alt="" />
                    </div>
                    <div className="language_name">
                      <p>Inglaterra</p>
                    </div>
                  </a>
                </li>
                <li>
                  <a href="" className="dropdown_option ">
                    <div className="language_flag">
                    <img src={portugueseImage} alt="" />
                    </div>
                    <div className="language_name">
                      <p>Portugal</p>
                    </div>
                  </a>
                </li>
              </ul>
            </div>
          </div>
              </div>
              <form className="search_container" onSubmit={handleSubmit}>
                <input
                  type="text"
                  className="escort_search"
                  value={search}
                  onChange={evt=>setSearch(evt.target.value)}
                  placeholder="Buscar números..."
                />
                <button className="search_button">
                  <div className="search_container">
                    <span className="material-symbols-outlined">search</span>
                  </div>
                </button>
              </form>
            </div>
    )
}

export default SearchNumber;