import { dataSource } from "../api/datasources";
import { deleteData, postDataWithoutPayload, postDataWithPayLoad, putData } from "../api/fetchData";
import userImage from "../assets/img/user_image.png";
import { useEffect, useState } from "react";
import AdminSearch from "./adminSearch";
import useAds from "../customHooks/useAds";
import TopNav from "./topNav";

export default function AdminAnnounces({setActivePage}:GeneralAdmin) {
  const { ads, updateAds } = useAds();

  //Adds to be filtered and show as results of search
  const [filteredAds, setFilteredAds] = useState(ads);

  //Create Adds
  const [newAdInfo, setNewAdInfo] = useState({
    name:"",
    description: "",
    ad:""
  })

  //Info of the comment selected by the user in the table of comments
  const [selectedAd, setSelectedAd] = useState(ads[0]);

  //Admin is editing
  const [adminEditing, setAdminEditing] = useState(false);

  const handleAdsDeletion = async (adId: string) => {
    try {
      await deleteData(`${dataSource}/ads/${adId}/delete`);
      updateAds();
    } catch (err) {
      console.log(err);
    }
  };

  const showAds = (adId: string) => {
    //Set the visibility of editor
    setAdminEditing(true);
    const userSelectedAd = ads.find(
      (ad) => ad._id === adId
    );
    if (userSelectedAd) {
      setSelectedAd(userSelectedAd);
    }
  };

  const handleChange = (evt: React.ChangeEvent<HTMLInputElement>) => {
    const value = evt.target.value;
    const id = evt.target.id;
    setSelectedAd((prevState) => ({ ...prevState, [id]: value }));
  };

  const handleSubmit = async (evt: React.FormEvent<HTMLFormElement>) => {
    evt.preventDefault();
    const selectedCommentInfo = new FormData();
    selectedCommentInfo.append("adInfo", JSON.stringify(selectedAd));
    const apiResponse = await putData(
      `${dataSource}/ads/${selectedAd._id}/update`,
      selectedCommentInfo
    );
    updateAds();
  };

  //update filtered Elements
  useEffect(() => {
    setFilteredAds(ads);
  }, [ads]);

  let tableContent: JSX.Element;

  if (filteredAds.length === 0) {
    tableContent = (
      <tbody>
        <tr>
          <td colSpan={5}>No hay anuncios para moderar...</td>
        </tr>
      </tbody>
    );
  } else {
    tableContent = (
      <tbody>
        {filteredAds.map((ad) => {
          return (
            <tr key={ad._id} onClick={() => showAds(ad._id)}>
              <td>{ad.name}</td>
              <td>{ad.description}</td>
              <td>
                <div className="ad_selector">
                  <select name="ad_position" id="ad_position">
                    <option value="zona1">Zona 1</option>
                    <option value="zona1">Zona 2</option>
                    <option value="zona1">Zona 3</option>
                  </select>
                </div>
              </td>
              <td>
                <div
                  className="btn_small success_theme"
                  onClick={() => handleAdsDeletion(ad._id)}
                >
                  Guardar
                </div>
              </td>
              <td>
                <div
                  className="btn_small escort_theme"
                  onClick={() => handleAdsDeletion(ad._id)}
                >
                  Eliminar
                </div>
              </td>
            </tr>
          );
        })}
      </tbody>
    );
  }

  return (
    <section className="comments_moderation">
      <TopNav title="ANUNCIOS" setActivePage={setActivePage}></TopNav>
      <main className="escort_admin_page">
        <AdminSearch
          propToSearch="description"
          originalArray={ads}
          setFiltered={setFilteredAds}
        />
        <div className="table_container">
          <table className="escorts_table my_sm_16">
            <thead>
              <tr>
                <td>Nombre</td>
                <td>Descripción</td>
                <td>Zona</td>
                <td></td>
                <td></td>
              </tr>
            </thead>
            {tableContent}
          </table>
        </div>
        <div className={`admin_editor ${adminEditing ? "show_container" : "hide"}`}>
          <div className="editor_header">
            <h2 className="txt_white">Editar Anuncio</h2>
            <div
              className="close_editor"
              onClick={() => setAdminEditing(false)}
            >
              <span className="material-symbols-outlined">cancel</span>
            </div>
          </div>
          <form className="escorts_form" onSubmit={handleSubmit}>
            <div className=" input_field_sm">
              <label htmlFor="name">Nombre del anuncio</label>
              <input
                type="text"
                id="name"
                value={selectedAd.name}
                onChange={handleChange}
              />
            </div>
            <div className=" input_field_sm">
              <label htmlFor="description">Descripción</label>
              <input
                type="text"
                id="description"
                value={selectedAd.description}
                onChange={handleChange}
              />
            </div>
            <div className=" input_field_sm">
              <label htmlFor="ad">Anuncio</label>
              <input
                type="text"
                id="ad"
                value={selectedAd.ad}
                onChange={handleChange}
              />
            </div>
            <button className="btn escort_theme">Modificar</button>
          </form>
        </div>
      </main>
    </section>
  );
}
