import { useEffect, useState } from "react";
import { dataSource } from "../api/datasources";
import { fetchData } from "../api/fetchData";
import "../types";

export default function useScripts() {
  const [scripts, setScripts] = useState<EscortScript[]>([{
    _id:"",
    name: "",
    description:"",
    script:""
  }]);

  const updateScripts = async () => {
    try {
      const apiPromise = await fetchData(
        `${dataSource}/scripts/getAll`
      );
      const apiResponse = (await apiPromise.json()) as EscortScript[];
      setScripts(apiResponse);
    } catch (err) {
      console.log(err);
    }
  };

  useEffect(() => {
    updateScripts();
  }, []);

  return { scripts,updateScripts };
}
